
















































import { Component, Vue } from 'vue-property-decorator'
import axios from 'axios'

import { isAxiosError } from '../utils'

@Component({
  components: {},
})
export default class LoginPage extends Vue {
  username = ''
  password = ''
  loading = false
  $refs!: {
    username: HTMLInputElement
  }
  showSnackbar = false
  snackbarMessage = ''

  mounted(): void {
    this.$refs.username.focus()
  }

  async login(): Promise<void> {
    this.loading = true

    try {
      const response = await axios.post<{ token: string }>('/auth/login', {
        username: this.username,
        password: this.password,
      })

      this.password = ''
      this.$store.commit('setSessionToken', response.data.token)
      this.$router.push('/')

      this.$store.dispatch('getUser')
      this.$store.dispatch('getPortfolios')
      this.$store.dispatch('getCurrencies')
    } catch (err) {
      if (isAxiosError(err) && err.response?.status === 401) {
        this.snackbarMessage = this.$tc('login.invalid-credentials')
      } else {
        this.snackbarMessage = String(err)
      }
      this.showSnackbar = true
    } finally {
      this.loading = false
    }
  }
}
